import React from 'react';

import './AboutMe.css';

const AboutMe = () =>{
    let rows = [
        {
            id:1,
            name:'Name:',
            info:'Nick Momotenko'
        },
        {
            id:2,
            name:'Interest:',
            info:'HTML development / Frontend'
        },
        {
            id:3,
            name:'Date of birth:',
            info:'October 24, 1997'
        },
        {
            id:4,
            name:'Skype:',
            info:'Nick Momotenko'
        },
        {
            id:5,
            name:'Email:',
            info:'nickmomotenkoforweb@gmail.com'
        },
        {
            id:6,
            name:'Telegram:',
            info:'Nick Momotenko'
        },

    ];

    return(
        <div className="about">
            <div className="about__title">
                Info 
            </div>
            <ul className="about__list">
                {rows.map(row=>{
                    return(
                        <li 
                            className="about__item"
                            key={row.id}
                        >
                            <div className="about__name">{row.name}</div>
                            <div className="about__info">{row.info}</div>
                        </li>
                    )
                })}
            </ul>
        </div>
    )
}

export default AboutMe;