import React, { Component } from 'react';

import './Gallery.css';

import GalleryItem from './GalleryItem';
import Title from '../Title';
import PageBar from '../PageBar';

import burgerImg from '../../assets/gallery/burger.png';
import elenaImg from '../../assets/gallery/elena.png';
import singoloImg from '../../assets/gallery/singolo.png';
import quarantieImg from '../../assets/gallery/quarantie.png';
import kinglyImg from '../../assets/gallery/kingly.png';
import planetImg from '../../assets/gallery/planet.png';
import pexImg from '../../assets/gallery/pex.png';
import bicycleImg from '../../assets/gallery/bicycle.png';
import tourismImg from '../../assets/gallery/tourism.png';
import wooderImg from '../../assets/gallery/wooder.png';
import creativiaImg from '../../assets/gallery/creativia.png';
import meandrImg from '../../assets/gallery/meandr.png';
import weddingImg from '../../assets/gallery/wedding.png';
import buildImg from '../../assets/gallery/build.png';

export default class Gallery extends Component{

    render(){
        let galleries = [
            {
                id:1,
                title:'Burger',
                img:burgerImg,
                link:'https://nickmomotenko.github.io/burger/',
            },
            {
                id:2,
                title:'Elena',
                img:elenaImg,
                link:'https://nickmomotenko.github.io/elena/',
            },
            {
                id:3,
                title:'Singolo',
                img:singoloImg,
                link:'https://nickmomotenko.github.io/singolo/',
            },
            {
                id:4,
                title:'Quarantie',
                img:quarantieImg,
                link:'https://nickmomotenko.github.io/quarantie/',
            },
            {
                id:5,
                title:'Kingly',
                img:kinglyImg,
                link:'https://nickmomotenko.github.io/kingly/',
            },
            {
                id:6,
                title:'Planet',
                img:planetImg,
                link:'https://nickmomotenko.github.io/portfolio/index.html',
            },
            {
                id:7,
                title:'Pex',
                img:pexImg,
                link:'https://nickmomotenko.github.io/pext/',
            },
            {
                id:8,
                title:'Bicycle',
                img:bicycleImg,
                link:'https://nickmomotenko.github.io/bicycle/index.html',
            }, 
            {
                id:9,
                title:'Tourism',
                img:tourismImg,
                link:'https://nickmomotenko.github.io/tourism/',
            },  
            {
                id:10,
                title:'Wooder',
                img:wooderImg,
                link:'https://nickmomotenko.github.io/wooder/',
            },
            {
                id:11,
                title:'Creativia',
                img:creativiaImg,
                link:'https://nickmomotenko.github.io/creativia/',
            },
            {
                id:12,
                title:'Meandr',
                img:meandrImg,
                link:'https://nickmomotenko.github.io/test-task-meandr/build/',
            }, 
            {
                id:13,
                title:'Wedding',
                img:weddingImg,
                link:'https://nickmomotenko.github.io/test-task-4-blocks/build/',
            },
            {
                id:14,
                title:'Build',
                img:buildImg,
                link:'https://nickmomotenko.github.io/kst-land/build/',
            },          
        ];
        
        return(
            <div className="gallery">
                <div className="content__wrapp">
                    <Title title="Latest Works" />
                    <ul className="gallery__list">
                        {galleries.map(gallery=>{
                            return(
                                <GalleryItem 
                                    key={gallery.id}
                                    gallery={gallery} 
                                />
                            )
                        })}
                    </ul>
                </div>
                <PageBar name="Gallery" {...this.props}/>
            </div>
        )
    }  
}
