import React, { Component } from 'react';

import Parallax from '../Parallax';
import Info from '../Info';

export default class Left extends Component{
    state = {
        cordMouseX: 0,
        cordMouseY: 0,
    }

    onMouseMove = (event) =>{
        this.setState({
            cordMouseX: event.nativeEvent.offsetX,
            cordMouseY: event.nativeEvent.offsetY,
        }) 
    }

    render() {
        return(
            <div className="display-left">
                <Parallax 
                    onMouseMove={this.onMouseMove} 
                    cordMouseX={this.state.cordMouseX} 
                    cordMouseY={this.state.cordMouseY} />
                <Info />
            </div>
        ) 
    }
}