import React, { Component } from 'react';
import './Form.css';

import Fallout from '../Fallout';

export default class extends Component{
    state ={
        name:'',
        email:'',
        message:'',

        nameError:'',
        emailError:'',
        messageError:'',

        isSubmit: false,
    }

    handleInputChange = ({name, value})=>{
        this.setState({
            [name]: value,
        })   
    }

    handleFormSubmit = (e) =>{
        e.preventDefault();

        const { name, email, message } = this.state;
        
        let isFormHasError = false;

        if(name === ''){
            this.setState({
                nameError:'Укажите ваше имя'
            })

            isFormHasError = true;
        }

        if(email === ''){
            this.setState({
                emailError:'Укажите ваш email'
            })

            isFormHasError = true;
        }

        if(message === ''){
            this.setState({
                messageError:'Укажите ваше сообщение'
            })

            isFormHasError = true;
        }

        if(!isFormHasError){
            
            this.setState({
                isSubmit: true,
                name:'',
                email:'',
                message:'',
                nameError:'',
                emailError:'',
                messageError:'',
            }) 
        }
    }

    render(){
        const { isSubmit } = this.state;

        return(
            <form className="form" onSubmit={this.handleFormSubmit}>
                <label className="form__label">
                    <span className="form__name">
                        Name
                    </span>
                    <Input type="text" name="name" value={this.state.name} handleInputChange={this.handleInputChange} />
                    <span className="form__error">{this.state.nameError}</span>
                </label> 
                <label className="form__label">
                    <span className="form__name">
                        Your email
                    </span>
                    <Input type="email" name="email" value={this.state.email} handleInputChange={this.handleInputChange} />
                    <span className="form__error">{this.state.emailError}</span>
                </label>
                <label className="form__label">
                    <span className="form__name">
                        Message
                    </span>
                    <Textarea name="message" value={this.state.message} handleInputChange={this.handleInputChange} />
                    <span className="form__error">{this.state.messageError}</span>
                </label>
                <div className="form__btn">
                    <button 
                        type="submit" 
                        className="btn" 
                    >
                        submit
                    </button>  
                    {
                        isSubmit 
                            ? 
                                <Fallout formSubmit={isSubmit}>
                                    Sorry... 
                                    The form processing server is under development
                                </Fallout> 
                            : 
                            null}
                </div>
                
            </form>
        )
    }
}

const Textarea = props =>{
    const {handleInputChange, name, value} = props;

    return <textarea className="form__textarea form__input" value={value} type="text" onChange={(e)=>handleInputChange({ name, value:e.target.value })}></textarea>
}

const Input = props =>{
    const { handleInputChange, name, value } = props;

    return <input className="form__input" type="text" value={value} onChange={(e)=>handleInputChange({ name, value:e.target.value })}></input>
}