import React from 'react';

import './Services.css';
import PageBar from '../PageBar';
import Title from '../Title';
import Accordeon from '../Accordeon';


import imgIdea1 from '../../assets/about/coffe.svg'
import imgIdea2 from '../../assets/about/lampa.svg'
import imgIdea3 from '../../assets/about/cloud.svg'

const Services = (props) =>{
    let services = [
        
        {
            id:1,
            name:'HTML/CSS development',
            icon: imgIdea1,
            info:'I use best practices for developing web projects. I will develop an individual website at the most modern level. I actively use all the best Html and CSS tools, preprocessors Sass and Scss, assemblers Gulp and Webpack. ',
        },
        {
            id:2,
            name:'Frontend',
            icon: imgIdea2,
            info:'If you want to revive your project, I will use some magic. Carousels, sliders, accordions, dropdowns, tooltips, form handlers are all not a problem. We will use this magic and your websites will be the most attractive and technological among all others',
        },
        {
            id:3,
            name:'Application',
            icon: imgIdea3,
            info:'I use and implement various technologies for developing web applications for clients. In particular, use and practice the small functions of the React and React Router. I am not good at this techlogology, but I am working on it.',
        },
    ];

    return(
        <div className="services">
            <div className="content__wrapp">
                <Title title="What I do" />
                <ServicesList services={services}/>
                <Title title="Some asked question" />
                <Accordeon />
            </div>
            <PageBar name="Services" {...props}/>
        </div>
    )
}

export default Services;

const ServicesList = (props) =>{
    const { services } = props;
    return(
        <ul className="services__list">
            {services.map((service)=>{
                return(
                    <li key={service.id} className="services__item">
                        <div className="services__icon">
                            <img src={service.icon} alt="Иконка" className="services__icon-pic"/>
                        </div>
                        <div className="services__info">
                            <div className="services__name about__title">{service.name}</div>
                            <div className="services__text about__info">
                                {service.info}
                            </div>
                        </div>
                    </li>
                )
            })}
        </ul>
    )
}

