import React, { Component } from 'react';

import './MainDisplay.css';

import Left from '../Display/Left';
import Right from '../Display/Right';

export default class MainDisplay extends Component{

    componentDidMount(){

        const { mainDisplayState, toggleMainDisplay } = this.props;

        if(!mainDisplayState){
            toggleMainDisplay();
        }
    }

    
    render(){
        const { toggleMainDisplay} = this.props;
        return(
            <div className="display">
                <Left />
                <Right 
                    toggleMainDisplay={toggleMainDisplay}
                />
            </div>
        )
    }
}