import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './PageBar.css';


export default class PageBar extends Component{

    render(){
        
        const { toggleMainDisplay } = this.props;

        return(
            <div className="page__bar" onClick={toggleMainDisplay}>
                <Link to="/" className="page__link">
                    <div className="page__cross"></div>
                    <div className="page__bg"></div>
                    <div className="page__name">{this.props.name}</div>
                </Link>
            </div>
        )
    }
}