import React , { Component } from 'react';

import './BetweenDisplay.css';

export default class BetweenDisplay extends Component{  
    
    render(){
        const { mainDisplayState } = this.props;

        return(
            <div 
                className="between"
            >
                <div className={mainDisplayState ? 'block b1' : 'block b1 blocks'}></div>
                <div className={mainDisplayState ? 'block b2' : 'block b2 blocks'}></div>
                <div className={mainDisplayState ? 'block b3' : 'block b3 blocks'}></div>
                <div className={mainDisplayState ? 'block b4' : 'block b4 blocks'}></div>
            </div>
        )
    }
}