import React, { Component } from 'react';

import './Parallax.css';
// import parallaxImg from '../../assets/1.jpg';
import X from '../../assets/3.png';

export default class Parallax extends Component{

    render(){
        const { onMouseMove, cordMouseX, cordMouseY } = this.props;

        return(
            <>
                <div className="parallax" onMouseMove={onMouseMove}>
                    <img 
                        src={X} 
                        alt="Картинка" 
                        className="parallax__img"
                        style={{transform:`rotateX(${cordMouseX/100}deg) rotateY(${cordMouseY/100}deg)`}}
                    />
                </div>
            </>
        )
    }
}