import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import './Page.css';

import Gallery from '../Gallery';
import About from '../About';
import Contact from '../Contact';
import Services from '../Services';

class Page extends Component{

    componentDidMount(){
        const { togglePageDisplay } = this.props;
        setTimeout(()=>{
            togglePageDisplay();
        },1000)
    }

    componentWillUnmount(){
        const { togglePageDisplay } = this.props;
        togglePageDisplay();  
    }

    render(){
        const { pageDisplayState } = this.props;

        return(
            <div 
                className="page"
                style={{opacity: pageDisplayState ? '1' : '0'}}
            >
                <div className="page__wrapp">
                    <div className="content">
                        <Route path="/page/about" render={props=> <About {...this.props} />} />
                        <Route path="/page/gallery" render={props=> <Gallery {...this.props} />} />
                        <Route path="/page/services" render={props=> <Services {...this.props} />} />
                        <Route path="/page/contact" render={props=> <Contact {...this.props} />} /> 
                    </div>
                </div>
            </div>
        )
    }
}

export default Page;