import React, { Component } from 'react';
import { Route } from 'react-router-dom';

import './App.css';

import MainDisplay from '../MainDisplay';
import Page from '../Page';
import BetweenDisplay from '../BetweenDisplay';
import Preloader from '../Preloader';

export default class App extends Component{

    state = {
        preloaderState: true,
        mainDisplayState: true,
        pageDisplayState: false,
    }

    togglePageDisplay = () =>{
        const { pageDisplayState } = this.state;

        this.setState({
            pageDisplayState : !pageDisplayState
        })
    }

    toggleMainDisplay = () =>{
        const { mainDisplayState } = this.state;

        this.setState({
            mainDisplayState : !mainDisplayState
        })
    }

    togglePreloader = () =>{
        const { preloaderState } = this.state;

        this.setState({
            preloaderState : !preloaderState,
        })
    }
    
    render(){
        return(
            <>
                <BetweenDisplay 
                    mainDisplayState={this.state.mainDisplayState}
                />
                <Route exact path="/" render={props=> <MainDisplay mainDisplayState={this.state.mainDisplayState} toggleMainDisplay={this.toggleMainDisplay} />} />
                <Route path="/page" render={props=> <Page mainDisplayState={this.state.mainDisplayState} togglePageDisplay={this.togglePageDisplay} pageDisplayState={this.state.pageDisplayState} toggleMainDisplay={this.toggleMainDisplay} />} />
                <Preloader 
                    togglePreloader= {this.togglePreloader} 
                    preloaderState={this.state.preloaderState} 
                />
            </>
        )
    }
};
