import React, { Component } from 'react';

import './Accordeon.css';

export default class Accordeon extends Component{
    state = {
        cards: [
            {
                id:1,
                name:'Some title',
                descr:'Some interesting description for this title ))',
            },
            {
                id:2,
                name:'Some title',
                descr:'Some interesting description for this title ))',
            },
            {
                id:3,
                name:'Some title',
                descr:'Some interesting description for this title ))',
            }
        ]
    };

    handleClickAccordeon = (e) =>{
        const clickedElem = e.currentTarget; 

        const name= clickedElem.firstElementChild;
        const triangle = name.nextElementSibling;

        const content = clickedElem.nextElementSibling;

        if(!content.classList.contains('active')){
            content.classList.add('active');
            triangle.style.transform = `rotate(180deg)`;   
        }else{
            content.classList.remove('active');
            clickedElem.classList.remove('triangle');
            triangle.style.transform = `rotate(0deg)`;    
        }
    }

    render(){
        const { cards } = this.state;
        
        return(
            <div className="accordeon">
                <ul className="accordeon__list">
                    {cards.map((card)=>{
                        return <AccordeonItem key={card.id} onClick={this.handleClickAccordeon} card={card}/>
                    })}
                </ul>
                                   
            </div>
        )
    }
}

const AccordeonItem = (props)=>{
    const { card } = props;

    return(
        <li className="accordeon__item">
            <div className="accordeon__title about__title" onClick={props.onClick}>
                <div className="accordeon__name ">
                    {card.name}
                </div>
                <div className="accordeon__triangle"></div>
            </div>
            <div className="accordeon__descr about__info">
                {card.descr}
            </div>
        </li>
    )
}
