import React from 'react';

import './Socials.css';
import vkImg from '../../assets/socials/vk.png';
import fbImg from '../../assets/socials/fb.png';
import gitImg from '../../assets/socials/git.png';
import telegramImg from '../../assets/socials/telegram.png';
import gmailImg from '../../assets/socials/gmail.png';

const Socials = () =>{
    let socials = [
        {
            id:1,
            link:'https://vk.com/saker_1',
            icon:vkImg,
        },
        {
            id:2,
            link:'https://www.facebook.com/profile.php?id=100015380190668',
            icon:fbImg,
        },
        {
            id:3,
            link:'https://github.com/NickMomotenko',
            icon:gitImg,
        },
        {
            id:4,
            link:'https://t.me/NickMomotenko',
            icon:telegramImg,
        },
        {
            id:5,
            link:'gmail',
            icon:gmailImg,
        },
    ];

    return(
       <div className="socials"> 
            <SocialsList socials={socials}/>
       </div> 
    )
}

export default Socials;

const SocialsList = (props) =>{

    const { socials } =props;

    return(
        <ul className="socials__list">
            {socials.map(social=>{
                return(
                    <SocialsItem key={social.id} social={social} />
                )
            })}
        </ul>
    )
}

const SocialsItem = (props) =>{
    const { social } = props;
    
    return (
        <li className="socials__item" >
            <a href={social.link} className="socials__link" target="_blank">
                <img src={social.icon} alt="Иконка" className="socials__img"/>
            </a>
        </li>
    )
}

