import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './MenuList.css';

const MenuItem = (props) =>{
    const { items, toggleMainDisplay } = props;    
    return(
        <>
            {items.map((item)=>{
                return (
                    <li key={item.id} className="menu__item" onClick={toggleMainDisplay}>
                        {/* <div className={`menu__bg menu__bg--${item.toLowerCase()}`}></div>
                        <div className="menu__name">{item}</div> */}
                        <Link to={item.path} className="menu__link">
                            <div className={`menu__bg menu__bg--${item.name.toLowerCase()}`}></div>
                            <div className="menu__name">{item.name}</div>
                        </Link>
                    </li>
                )
            })}
        </>
    )
}

export default class MenuList extends Component {
    render() {
        const items = [
            {
                id:1,
                name:"About",
                path:'/page/about',
            },
            {
                id:2,                
                name:"Portfolio",
                path:'/page/gallery',
            },
            {
                id:3,                                
                name:"Services",
                path:'/page/services',
            },
            {
                id:4,                
                name:"Contact",
                path:'/page/contact',
            },
        ];

        const { toggleMainDisplay } = this.props;
        
        return (
            <nav className="menu">
                <ul className="menu__list">
                    <MenuItem items={items} toggleMainDisplay={toggleMainDisplay}/>
                </ul>
            </nav>
        )
    }
}


