import React, { Component } from 'react';
import './Gallery.css';

class GalleryItem extends Component{

    state = {
        cordX : 0,
        cordY : 0,
        halfItemHeight: 0,
        underCard: false,
        canBeAnimate: true,
    }

    componentDidMount(){
        if(window.innerWidth < 480){
            this.setState({
                canBeAnimate: false,
            })
        }
    }

    cardMouseMove = (event) =>{
        this.setState({
            cordX:event.nativeEvent.offsetX,
            cordY:event.nativeEvent.offsetY,
            halfItemHeight:100,
            underCard: true,
        })
    }

    cardMouseOut = () =>{
        this.setState({
            cordX: 0,
            cordY: 0,
            halfItemHeight:0,
            underCard: false,
        })
    }

    openLink = (e) =>{
        e.preventDefault();
        
        window.open(e.target.href , '_blank');
    }

    render(){
        const { gallery } = this.props;
        const { cordX, cordY, halfItemHeight, underCard, canBeAnimate } =this.state;
        
        return (
            <>
                <li 
                    className="gallery__item"
                    onMouseMove={this.cardMouseMove}
                    onMouseOut={this.cardMouseOut}
                    style={{transform:underCard && canBeAnimate && 'rotateX(' + -(cordX - halfItemHeight)/7 + 'deg) rotateY(' + -(cordY - halfItemHeight)/7 + 'deg)'}}
                >
                    <a href={gallery.link} className="gallery__link" onClick={this.openLink}>
                        {gallery.title}
                    </a>
                    <div className="gallery__img">
                        <img src={gallery.img} alt="Картинка" className="gallery__img-pic"/>
                    </div>
                   
                </li>
            </>
        )
    }
    
}

export default GalleryItem;

