import React from 'react';

import './StoryDesk.css';

import imgIdea1 from '../../assets/about/coffe.svg';
import imgIdea2 from '../../assets/about/lampa.svg';
import imgIdea3 from '../../assets/about/cloud.svg';


import imgFirst from '../../assets/about/1.png';
import imgSecond from '../../assets/about/2.png';
import imgThird from '../../assets/about/3.png';

const StoryDesk = () =>{
    let rows = [
        {
            id:1,
            name:'Cups of tea',
            count: '2500',
            imgIdea:imgIdea1,
            imgInTheCorner:imgFirst,
        },
        {
            id:2,
            name:'Training time',
            count: 'Can not count', 
            imgIdea:imgIdea2, 
            imgInTheCorner:imgSecond,
        },
        {
            id:3,
            name:'Completed projects ',
            count: 'more than 15', 
            imgIdea:imgIdea3,
            imgInTheCorner: imgThird,
        },
    ];
    
    return(
        <div className="story-desk">
            <ul className="story-desk__list">
                {rows.map((row)=>{
                    return(
                        <li className="story-desk__item" key={row.id}>
                            <div className={`story-desk__img story-desk__img--${row.id}`}>
                                <img src={row.imgInTheCorner} alt="Story desc" className="story-desk__img-pic"/>
                            </div>
                            <div className="story-desk__icon">
                                <img src={row.imgIdea} alt="Иконка" className="story-desk__icon-pic"/>
                            </div>  
                            <div className="story-desk__name">{row.name}</div>
                            <div className="story-desk__count">{row.count}</div>
                        </li>
                    )
                })}
            </ul>
        </div>
    )
}

export default StoryDesk;