import React from 'react';

import './Skills.css';


const Skills = (props) =>{
    let skills = [
        {
            id:1,
            title:'HTML / CSS',
            experience: '~ 1.5 year experience',
            count:'95%',
        },
        {
            id:2,
            title:'SCSS, SASS, Flexbox, BEM, GRID',
            experience: '~ 1 year experience',
            count:'90%',
        },
        {
            id:3,
            title:'Adobe Photoshop , Adobe Assets, Avacode',
            experience: '~ 1 year experience',
            count:'90%',
        },
        {
            id:4,
            title:'JS',
            experience: '~ 6 months experience',
            count:'65%',
        },
        {
            id:5,
            title:'React JS ,React Router',
            experience: '~ 1 -1.5 month experience',
            count:'40%',
        },
    ];

    return(
        <div className="skills">
            <div className="skills__title" onClick={props.onClick}>
                My skills
            </div>
            <ul className="skills__list">
                {skills.map(skill=>{
                    return (
                        <li className="skills__item" key={skill.id}>
                            <div className="skills__info">
                                <div className="skills__name">{skill.title}</div>
                                <div className="skills__experience">{skill.experience}</div>
                            </div>
                            <div className="skills__line">
                                <div 
                                    className="skills__line-over"
                                    style={{width: skill.count}}
                                ></div>
                                <div className="skills__line-count">{skill.count}</div>
                            </div>
                        </li>
                    )
                })}
            </ul>
        </div>
    )
}

export default Skills;